import React from "react";
import Link from "next/link";
import MainLayout from "../layouts/MainLayout";
export default function Index() {
  return (
    <MainLayout>
      <h2>Top công cụ hỗ trợ chơi game Thiên Long Bát Bộ</h2>
      <p>
        Công cụ hỗ trợ quá trình làm acc, hay lão hữu quy lai muốn tìm đúng máy
        chủ ngày xưa để tham gia!!
      </p>

      <ul>
        <li>
          <Link href="/bao-thach">
            <a>Tính số lượng bảo thạch cần để hợp thành lên cấp mục tiêu</a>
          </Link>
        </li>
        <li>
          <Link href="/nhuan-hon-thach">
            <a>
              Tính cấp Nhuận hồn thạch cao nhất có thể hợp được dựa vào số lượng
              được khai báo
            </a>
          </Link>
        </li>
        <li>
          <Link href="/game-server">
            <a>
              Tra cứu lịch sử máy chủ: tên máy chủ gốc, các máy chủ được hợp,
              Đại Thế Giới mà máy chủ đó trực thuộc
            </a>
          </Link>
        </li>
      </ul>
    </MainLayout>
  );
}
